(function() {
  'use strict';

  angular
    .module('GradeTrack')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("GradeTrack").run(["$templateCache", function($templateCache) {$templateCache.put("app/assignments/assignments.html","<div ng-controller=\"AssignmentsController as vm\"><h1>{{vm.activeCourse()}}</h1></div>");
$templateCache.put("app/grades/grades.html","Grades Area");
$templateCache.put("app/information/information.html","Information Area");
$templateCache.put("app/students/students.html","Students Area");}]);